video{
    width: 100%;
  height: 100vh;
  object-fit: cover;
}

.content{
    position: absolute;
    width: 100%;
    height: 100%;
    font-size: 30px;
    display: flex;
    flex-direction: column;
    /* left: 10%; */
    align-items: center;
    justify-content: center;
}

.overlay {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.4);
    /* background-color: #000000cc; */
  }